@import "../utils/Variables.scss";
/* TwoContainerComponent.css */

.twoContainer {
  display: flex;
  color: $text-color;
}

.leftContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  width: 50%;
  flex: 1;
  padding: 20px;
}

.portfolioBtn {
cursor: pointer;
width: 150px;
background-color: white; 
border: 1px solid$hover-color;
color: $text-color;
padding: 10px 20px; 
transition: background-color 0.5s ease, color 0.5s ease; 
margin-top: 20px;
}
.portfolioBtn:hover {
  background-color:$hover-color ; 
  color: white;
}

.rightContainer {
  flex: 1;
  padding: 20px;
  height: 100vh;
  width: 50%;
}
.headingTwo {
  font: 30px $heading-font;
  border-bottom: 1px solid $hover-color;
  padding-bottom: 20px; 
  display: inline-block;
}
.paragraph {
  font: 20px $basic-font;
  text-align: center;
  padding-left: 3%;
  padding-right: 3%;
}


@media (max-width: 820px) {
  .twoContainer {
  display: flex;
  flex-direction: column ;
  }
  .leftContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 50vh;
    width: 100%;
  }
  .headingTwo {
    font: 20px $heading-font;
    padding-top: 5%;
  }
  .paragraph {
    font: 14px $basic-font;
  }
  .rightContainer {
    flex: 1;
    height: 50vh;
    width: 100%;
  }
  .lampsImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  }
}




@media (min-width: 769px) and (max-width: 1024px) {

  .containerBox {
    width: 80vw;
  }
  
  
}









